.terminal
{
    position: relative;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    width: 100%;

    -webkit-transition: opacity 5s ease-out;

    -o-transition: opacity 5s ease-out;

    transition: opacity 5s ease-out;

    color: #3DF500;
    border: #4B3D22 20px ridge;
    border-bottom-width: 40px;
    border-radius: 15px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#34342C), to(#1F1E1A));
    background-image: -webkit-linear-gradient(top, #34342C, #1F1E1A);
    background-image: -o-linear-gradient(top, #34342C, #1F1E1A);
    background-image: linear-gradient(to bottom, #34342C, #1F1E1A);
    -webkit-box-shadow: inset 0 0 100px rgba(0,0,0,.3), inset 0 15px 20px rgba(0,0,0,.7);
            box-shadow: inset 0 0 100px rgba(0,0,0,.3), inset 0 15px 20px rgba(0,0,0,.7);
    text-shadow: .1em .1em rgba(0,0,0,.7);
}

.terminal a
{
    color: #3DF500;
}

.terminal a:hover
{
    color: #fff;
}

.terminal_text
{
    padding: 15px;
}

.terminal + .terminal
{
    margin-top: 35px;
}

.terminal__title
{
    position: absolute;
    bottom: 100%;
    left: 0px;

    margin-bottom: 10px;
    padding: 2px 0 0 8px;

    border-width: 10px;
    border-style: ridge;
    border-color: #4B3D22;
    border-bottom-width: 0;
    border-radius: 6px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#34342C), to(#1F1E1A));
    background-image: -webkit-linear-gradient(top, #34342C, #1F1E1A);
    background-image: -o-linear-gradient(top, #34342C, #1F1E1A);
    background-image: linear-gradient(to bottom, #34342C, #1F1E1A);
    -webkit-box-shadow: inset 0 0 8px rgba(0,0,0,.3), inset 0 6px 6px rgba(0,0,0,.7);
            box-shadow: inset 0 0 8px rgba(0,0,0,.3), inset 0 6px 6px rgba(0,0,0,.7);

    line-height: 1.2em;
}

.terminal.hide {
    opacity: 0;
}

.terminal.hidden {
    opacity: 0;
}


